<template>
  <v-container>
    <v-card>
      <v-toolbar>
        <v-toolbar-title>Resultat setzen </v-toolbar-title>
      </v-toolbar>
      <v-list subheader>
        <v-subheader>Daten der Person</v-subheader>
        <v-row>
          <v-col>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Vorname</v-list-item-title>
                <v-text-field
                  v-model="firstname"
                  :rules="nameRules"
                  :counter="10"
                  label="First name"
                  required
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Nachname</v-list-item-title>
                <v-text-field
                  v-model="lastname"
                  :rules="nameRules"
                  :counter="10"
                  label="Last name"
                  required
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
          </v-col>
          <v-col>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title>Geb. Datum</v-list-item-title>
                <v-list-item-subtitle>
                  <v-text-field
                    v-model="gebdatum"
                    :rules="nameRules"
                    :counter="10"
                    label="Last name"
                    required
                  ></v-text-field
                ></v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
          </v-col>
        </v-row>
      </v-list>
      <v-divider></v-divider>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="blue darken-1"
          text
          :disabled="sure"
          :loading="sure"
          class="white--text"
          @click="sure = true"
        >
          Fertig
        </v-btn>
      </v-card-actions>
    </v-card>
    <v-dialog v-model="sure" hide-overlay persistent width="300">
      <v-card color="black" dark>
        <v-card-text>
          Resultat wird gesetzt
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  watch: {
    sure(val) {
      if (!val) return;

      setTimeout(() => ((this.sure = false), this.addEmployee()), 2000);
    },
  },
  data() {
    return {
      sure: false,
      firstname: '',
      gebdatum: '',
      lastname: '',
      nameRules: [
        (v) => !!v || 'Name is required',
        (v) => v.length <= 10 || 'Name must be less than 10 characters',
      ],
    };
  },
  methods: {
    addEmployee() {},
  },
};
</script>

<style lang="scss" scoped></style>
